import {
    Component,
    OnInit,
    ViewChild,
    TemplateRef,
    AfterViewInit,
    ChangeDetectorRef
} from "@angular/core";
import { MenuItem } from "../../../shared/classes/menu";
import { FormulaireService } from "../../services/formulaire.service";
import { Router } from "@angular/router";
import { Subscription } from "rxjs/Subscription";
import { ListComponent } from "../list/list.component";

declare var $: any;

@Component({
    selector: "blog-accueil",
    templateUrl: "./accueil.component.html",
    styleUrls: ["./accueil.component.scss"]
})
export class AccueilComponent implements OnInit {
    subscription: Subscription;

    @ViewChild(ListComponent) blogList: ListComponent;

    actions = {
        refresh_list_accueil: data => this.refreshList()
    };

    constructor(private FormulaireService: FormulaireService, private router: Router) {}

    ngOnInit() {
        this.subscription = this.FormulaireService.events.subscribe(
            $e => this.actions[$e.action] && this.actions[$e.action]($e)
        );
    }

    refreshList() {
        // this.blogList.getPublish();
        // this.blogList.getUnPublish();
    }

    goTo(where: string) {
        if (where === "home") {
            this.router.navigate(["/"]);
        }
    }
}



