import { Component, OnInit, OnDestroy, ViewChild } from "@angular/core";
import { Site } from "../../../api";
import { StoreService } from "../../../shared/services/store.service";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { Subject } from "rxjs/Subject";
import { BlogsService, ArticlePagination, ArticlesService } from "../../../api-blog";
import { ImageLibComponent } from "../../../shared/components/image-lib/image-lib.component";
import { FormulaireService } from "../../services/formulaire.service";
import { Router } from "@angular/router";
import { AppService } from "../../../global/services/app.service";
import { FormulairesService } from "../../../api-form";
import { ErrorsService } from "../../../global/services/errors.service";

declare var moment: any;

@Component({
    selector: "formulaire-list",
    templateUrl: "./list.component.html",
    styleUrls: ["./list.component.scss"]
})
export class ListComponent implements OnInit, OnDestroy {
    @ViewChild("modalDomain") modalDomain: ModalComponent;
    @ViewChild("modalDeleteSite") modalDeleteSite: ModalComponent;
    @ViewChild(ImageLibComponent) imglib: ImageLibComponent;
    @ViewChild("modalDeletePost")
    modalDeletePost: ModalComponent;

    // Array des sites de l'user
    sites: Site[];

    // determine l'ordre du listing des sites
    listConf = {
        order: ""
    };

    // indique si les sites sont chargés
    areLoadedSites: boolean = false;
    state: any = {};

    btnEditArticle = {
        label: "Editer",
        icon: { active: true, className: "fas fa-pencil-alt" },
        className: "we-btn we-btn-block we-btn-secondary we-mb-2"
    };

    btnDeleteArticle = {
        label: "Supprimer",
        icon: { active: true, className: "far fa-trash-alt" },
        className: "we-btn we-btn-block we-btn-danger"
    };

    articles = [];
    articles_unpublished = [];

    pagination: ArticlePagination = { items: [] };
    pagination_unpublished: ArticlePagination = { items: [] };

    pagination_loaded = false;
    pagination_unpublished_loaded = false;

    page_published = 1;
    page_unpublished = 1;

    actions = {};

    showLists = { published: true, unpublished: false };

    wantAddImg = false;

    ItemImageToUpdate = null;

    confLibImg = {
        tabs: {
            freeImg: { active: true },
            biblio: { active: true },
            internet: { active: true },
            color: { active: false }
        }
    };

    itemToDelete = null;
    reloadPublish = null;
    loadedList = false;

    constructor(
        private FormApi: FormulairesService,
      //  private FormulaireService: FormulaireService,
        private articleApi: ArticlesService,
        public storeService: StoreService,
        private router: Router,
        public appService: AppService,
        private errorsService: ErrorsService,
    ) {}

    formatDate(d) {
        return moment(d).fromNow();
    }

    ngOnInit() {
        this.getPublish();

    }

    onChangePagePublish($event) {
        this.page_published = $event.page;
        this.getPublish();
    }



    wantDelete(item, publish) {
        this.itemToDelete = item;
        this.reloadPublish = publish;
        this.modalDeletePost.open();
    }

    deletePost() {
        const idToDelete = this.itemToDelete.id;
        let formId = this.storeService.curSite.uids.find(u => u.nom === "form");
        this.FormApi.apiSitesSiteFormulairesSlugDelete(formId.distant_id.toString(),String(idToDelete)).subscribe(() => {
            this.loadedList = false;
            this.getPublish();
            this.modalDeletePost.close();
        });
    }

    parseGroupes(item){

        let groupes = [];

        if(typeof item.groupes != "undefined"){
            let parsed = JSON.parse(item.groupes);
            if(typeof parsed ){
                groupes = JSON.parse(item.groupes);
            }
        }

        return groupes;
    }

    getPublish() {

        let formId = this.storeService.curSite.uids.find(u => u.nom === "form");

        this.FormApi.apiSitesSiteFormulairesModuleIdModuleMeGet(
            formId.distant_id.toString(),
            "0"
            ).subscribe(
            e => {
            this.pagination = e;
            let articles = e.items;
            articles.map(art => {
                art['groupesFormatted'] = this.parseGroupes(art);
            });

            this.articles = articles;
            this.pagination_loaded = true;
            this.loadedList = true;
            },
            error => {
                this.errorsService.handleErrorHttp(error);

            }
        );



    }





    ngOnDestroy() {
        // prevent memory leak when component destroyed
        this.state = null;
    }


    clickOnRowItem(event: Event, item) {
        const canNavigate = event.srcElement && event.srcElement.nodeName !== "BUTTON" && event.srcElement.nodeName !== "I";

        if (canNavigate) {
            this.router.navigate(["/formulaire", "formulaires", "edit", item.id]);
        }
    }
}



