import {
    Component,
    OnInit,
    OnDestroy,
    ViewChild,
    HostListener
} from "@angular/core";
import {
    Site,
    SitesService,
    PageType,
    VersionsService as VersionsPage,
    ContainerType,
    PagesService,
    PageHistory,
    Page,
    MetaType,
    Meta
} from "../../../api";

import { Router, ActivatedRoute } from "@angular/router";

import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { Subject } from "rxjs/Subject";
import {
    BlogsService,
    ArticlesService,
    VersionsService,
    ArticleType,
    Article,
    ArticleHistory
} from "../../../api-blog";
import { ImageLibComponent } from "../../../shared/components/image-lib/image-lib.component";
import { NgForm, FormBuilder, FormGroup, FormControl } from "@angular/forms";
import { Observable } from "rxjs/Observable";
import { ImageLibService } from "../../../shared/services/image-lib.service";
import { StoreService } from "../../../shared/services/store.service";
import { FormulaireService } from "../../services/formulaire.service";
import { ComponentCanDeactivate } from "../../../shared/_guards/pending-changes.guard";
import { slug, getSrcSet } from "../../../shared/utils/utils";
import { ErrorsService } from "../../../global/services/errors.service";
import { ModulesService } from "../../../api/api/modules.service";
import { Breadcrumb } from "../../../shared/components/breadcrumb/breadcrumb.component";
import { AppService } from "../../../global/services/app.service";
import { DraftEditorComponent } from "../../../shared/components/draft-editor/draft-editor.component";
import {
    FormulaireResponsesService,
    FormulairesService,
    Formulaire,
    FormulaireReponse,
    FormulaireReponsePagination
} from "../../../api-form";
declare var moment: any;
@Component({
    selector: "blog-edit",
    templateUrl: "./edit.component.html",
    styleUrls: ["./edit.component.scss"]
})
export class EditComponent implements OnInit, OnDestroy {
    // @HostListener("window:beforeunload") allows us to also guard against browser refresh, close, etc.
    @HostListener("window:beforeunload")
    objectKeys = Object.keys;

    // tslint:disable-next-line:member-ordering
    @ViewChild(ModalComponent)
    modalMediaLib: ModalComponent;
    @ViewChild(ImageLibComponent) imgLib: ImageLibComponent;
    @ViewChild(NgForm) f1: NgForm;

    // @ViewChild("bodyEditor") bodyEditor;
    @ViewChild("draftEditor")
    draftEditor: DraftEditorComponent;

    form: FormGroup;
    draftCpt;

    // Array des sites de l'user
    sites: Site[];

    // determine l'ordre du listing des sites
    listConf = {
        order: ""
    };

    // indique si les sites sont chargés
    areLoadedSites: boolean = false;

    state: any = {};

    createSite$ = new Subject();

    articleCategories = [];

    btnEditArticle = {
        label: "Editer",
        icon: { active: true, className: "fas fa-pencil-alt" },
        className: "we-btn we-btn-block we-btn-secondary we-mb-2"
    };

    btnDeleteArticle = {
        label: "Supprimer",
        icon: { active: true, className: "far fa-trash-alt" },
        className: "we-btn we-btn-block we-btn-danger"
    };

    article: Formulaire;
    formulaireReponses: FormulaireReponse[];

    canAdd: boolean = false;
    isCustomForm = true;
    uid = {};
    mode = "Edition";
    titleSidebar = "";

    pathBlog: string = "blog/";
    TabKeys = [];

    pagination: FormulaireReponsePagination = { items: [], page: 1 };
    pagination_unpublished: FormulaireReponsePagination = { items: [] };
    pagination_loaded = false;
    pagination_unpublished_loaded = false;

    breadcrumbs: Breadcrumb[] = [];
    isLoaded = false;
    loadedList = false;

    constructor(
        private router: Router,
        private storeService: StoreService,
        private route: ActivatedRoute,

        public appService: AppService,
        public FormReponseApi: FormulaireResponsesService,
        public FormApi: FormulairesService
    ) {
        this.form = new FormBuilder().group({
            chips: [["chip"], []]
        });

        if (typeof this.storeService.curSite !== "undefined") {
            this.uid = this.storeService.curSite["uid"];
        }
    }

    ngOnInit() {
        if (this.route.snapshot.params.id) {
            this.initArticle();
        } else {
            this.mode = "Nouveau";
        }
    }

    getTabValue(item) {
        return Object.values(item.data);
    }

    onChangePage($event) {
        //console.log("Event", $event);
        this.pagination.page = $event.page;
        this.loadedList = false;
        this.isLoaded = false;
        this.initArticle();
    }

    formatDate(d) {
        return moment(d).fromNow();
    }

    initArticle() {
        let formId = this.storeService.curSite.uids.find(u => u.nom === "form");

        this.FormApi.apiSitesSiteFormulairesSlugGet(
            formId.distant_id.toString(),
            this.route.snapshot.params.id
        ).subscribe(e => {
            let article = e;
            let groupes = [];
            if (typeof article["groupes"] != "undefined") {
                article["groupesF"] = JSON.parse(article["groupes"]);
            } else {
                article["groupesF"] = groupes;
            }

            this.article = article;
            this.isCustomForm = (article.type == 'custom');

            this.FormReponseApi.apiFormulairesSlugReponsesGet(
                this.route.snapshot.params.id,
                this.pagination.page
            ).subscribe(b => {
                this.pagination = b;
                let articles = b.items;
                articles.map(article => {
                    article["data"] = JSON.parse(article.data);
                });

                if (articles.length > 0) {
                    this.TabKeys = Object.keys(articles[0]["data"]);
                    this.TabKeys = this.TabKeys.filter(
                        k => k !== "recaptcha" && k !== "rgpd" && k !== "Email"
                    );
                }

                this.pagination_loaded = true;
                this.loadedList = true;
                this.formulaireReponses = articles;
                this.isLoaded = true;
            });
        });
    }

    ngOnDestroy() {
        this.state = null;
    }

    goToPosts() {
        this.router.navigate(["blog"]);
    }
}



