    getArticles( onSuccess?: Function, onError?: Function) {


        console.log("UID :", this.storeService.curSite.uid);
        let formId = this.storeService.curSite.uids.find(u => u.nom === "form");

        this.formApi.apiSitesSiteFormulairesModuleIdModuleMeGet(
            formId.distant_id.toString(),
            "0"
            ).subscribe(
            e => {
                this.articles = e.items;
                if (typeof onSuccess === "function") {
                    onSuccess(e);
                }
            },
            error => {
                this.errorsService.handleErrorHttp(error);
                if (typeof onError === "function") {
                    onError(error);
                }
            }
        );
    }

