import {
    Component,
    OnInit,
    ViewChild,
    TemplateRef,
    ChangeDetectorRef
} from "@angular/core";
import { FormulaireService } from "../../services/formulaire.service";
import { MenuItem } from "../../../shared/classes/menu";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { AppConf } from "../../../global/app.conf";

declare var $: any;

@Component({
    selector: "blog-navigation-sidebar",
    templateUrl: "./navigation-sidebar.component.html",
    styleUrls: ["./navigation-sidebar.component.scss"]
})
export class NavigationSidebarComponent implements OnInit {
    maxCategories = 10;

    @ViewChild("tplPost") tplPost: TemplateRef<any>;
    @ViewChild("tplCat") tplCat: TemplateRef<any>;
    @ViewChild("modalDeletePost")
    modalDeletePost: ModalComponent;

    itemToDelete = null;

    actions = {};

    itemsCats: MenuItem[] = [
        {
            id: "blog_list_posts",
            iconL: { active: true, className: "fal fa-copy" },
            iconR: AppConf.NAV_ITEM.iconR,
            name: "Vos Formulaires",
            routerLink: "/formulaire/formulaires",
            className: AppConf.NAV_ITEM.css,
            linkFill: true,
            type: "page"
        }
    ];

    constructor(public FormulaireService: FormulaireService) {}

    ngOnInit() {}

    handleAction($e) {
        return $e && this.actions[$e.action] && this.actions[$e.action]($e);
    }
}



